:root {
	--selected-background: #34495e;
	--selected-color: #fff;
	--disabled-color: #bbb;
	--component-background: #fff;
	--component-color: #424242;
	--underlay-background: rgba(66, 66, 66, 0.3);
	--underlay-background: rgba(66, 66, 66, 0.3);

	--error-color: red;
	--success-color: green;

	--short-animation-duration: .2s;
	--short-animation-easing: ease;

	/* Z-index levels */
	--zindex-base: 0;
	--zindex-tooltip: 100;
	--zindex-dropdown: 200;
	--zindex-fixed: 300;
	--zindex-dialog: 400;
	--zindex-alert: 500;
}

.root {}

.slideIn,
.slideOut {
	-webkit-transition: -webkit-transform ease-in-out .2s;
	transition: -webkit-transform ease-in-out .2s;
	transition: transform ease-in-out .2s;
	transition: transform ease-in-out .2s, -webkit-transform ease-in-out .2s;
	-webkit-transition: -webkit-transform ease-in-out var(--short-animation-duration);
	transition: -webkit-transform ease-in-out var(--short-animation-duration);
	transition: transform ease-in-out var(--short-animation-duration);
	transition: transform ease-in-out var(--short-animation-duration), -webkit-transform ease-in-out var(--short-animation-duration);
}

.underlayVisible {
	background: rgba(66, 66, 66, 0.3);
	background: var(--underlay-background);
	z-index: 400;
	z-index: var(--zindex-dialog);
}

.content { }

.open { }

.pane {
	background-color: #fff;
	background-color: var(--component-background);
	z-index: calc(400 + 1);
	z-index: calc(var(--zindex-dialog) + 1);
}

.title {
	position: relative;
}

.close {
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	right: 35px;
	cursor: pointer;
	font-size: 0;
	border: none;
	background: none;
}

.close i {
	font-size: 14px;
}

.left { }

.right { }

.top { }

.bottom { }



