:root {
	--selected-background: #34495e;
	--selected-color: #fff;
	--disabled-color: #bbb;
	--component-background: #fff;
	--component-color: #424242;
	--underlay-background: rgba(66, 66, 66, 0.3);
	--underlay-background: rgba(66, 66, 66, 0.3);

	--error-color: red;
	--success-color: green;

	--short-animation-duration: .2s;
	--short-animation-easing: ease;

	/* Z-index levels */
	--zindex-base: 0;
	--zindex-tooltip: 100;
	--zindex-dropdown: 200;
	--zindex-fixed: 300;
	--zindex-dialog: 400;
	--zindex-alert: 500;
}

@-webkit-keyframes fadeIn {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

@keyframes fadeIn {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

.fadeIn {
	-webkit-animation-duration: .2s;
	        animation-duration: .2s;
	-webkit-animation-duration: var(--short-animation-duration);
	        animation-duration: var(--short-animation-duration);
	-webkit-animation-fill-mode: both;
	        animation-fill-mode: both;
	-webkit-animation-name: fadeIn;
	        animation-name: fadeIn;
}

@-webkit-keyframes fadeOut {
	from {
		opacity: 1;
	}
	to {
		opacity: 0;
	}
}

@keyframes fadeOut {
	from {
		opacity: 1;
	}
	to {
		opacity: 0;
	}
}

.fadeOut {
	-webkit-animation-duration: .2s;
	        animation-duration: .2s;
	-webkit-animation-duration: var(--short-animation-duration);
	        animation-duration: var(--short-animation-duration);
	-webkit-animation-fill-mode: both;
	        animation-fill-mode: both;
	-webkit-animation-name: fadeOut;
	        animation-name: fadeOut;
}



